html, body {
  height: 100%;
  margin: 0px 20px 0px 20px;
}

body {  
  font-family: Arial, Verdana, sans-serif;
}

.wrapper {
  width: 980px;
  float: left;
  padding: 0px 20px 0px 20px;
  margin: 0px;
  border: 0px solid #000000;
  height: 100%;
  margin: 0px;
}

section {
  width: 600px;
  padding: 10px 0px 0px 100px;
  float: right;
  font-size: larger;
  margin: 0px;
}

p.post_date {
  font-size: x-small;
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.post_date_large {
  font-size: small;
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.post_date_small {
  font-size: xx-small;
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.post_title {
  font-size: xx-large;
  font-weight: bold;
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.post_title_small {
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.read_more {
  font-size: small;
  padding: 0px 0px 0px 0px;
  margin: 0px;
}

p.under_post {
  font-size: medium;
  padding: 0px 0px 0px 0px;
  margin: 40px 0px 20px 0px;
}

li {
  padding: 0px 0px 5px 0px;
}

ul.home {
  list-style: none;
  padding: 80px 0px 20px 0px;
  margin: 0px;
}

li.home {
  padding: 0px 0px 70px 0px;
}

ul.blog {
  list-style: none;
  padding: 20px 0px 20px 0px;
  margin: 0px;
}

li.blog {
  padding: 0px 0px 20px 0px;
}

table {
  min-width: 480px;
}

tr.blog {
  vertical-align: top;
  padding: 10px 0px 0px 0px;
}

td.blog_post_date {
  width: 70px;
}

hr.under_post {
  width: 200px;
  float: left;
}

aside {
  width: 240px;
  height: 100%;
  padding: 10px 0px 0px 0px;
  font-size: larger;
  border-right: 1px solid gray;
  margin: 0px;
  position: fixed;
}

aside img.avatar {
  width: 200px;
}
  
a {
  color: blue;
  text-decoration: none;
}

aside a.current {
  font-weight: bold;
}

a.black_link {
  color: black;
}

aside ul {
  list-style: none;
  padding: 50px 0px 0px 30px;
}

aside li {
  padding: 0px 0px 5px 0px;
}

aside p.copyright {
  font-size: small;
  position: absolute;
  bottom: 0px;
}
